.menu {
    position: fixed;
    top: 20px;
    left: 20px;
    background-color: rgb(148, 31, 31);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    padding: 15px 10px;
    border-radius: 3px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    z-index: 10;
    &:hover {
        background-color: rgb(92, 92, 92);
    }
}

/*______________________________________________________________toggleMenu______________________________________________*/

.sidebar {
    width: 400px;
    height: 100vh;
    background-color: white;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @media screen and(max-width: 800px) {
        width: 100vw;
    }
}

.sidebar-header {
    background-color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
    h1 {
        color: rgb(32, 32, 32);
        font-weight: 900;
        font-size: 1.9rem;
        text-align: center;
    }
}

.logo-img {
    width: 160px;
    img {
        width: 100%;
    }  
}

.close-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(32, 32, 32);
    font-size: 3.1rem;
    padding: 12px;
    margin: 0 10px;
    border-radius: 3px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    &:hover {
        background-color: rgb(148, 31, 31);
    }
}


.nav-menu {
    width: 100%;
    display: grid;
    grid-template-rows: repeat(5, 80px); 
        .navlink {
            display: flex;                            
            justify-content: center;
            align-items: center;
            border-bottom: 2px solid white;
            font-size: 1.5rem;
            color: white;
            text-decoration: none;
            width: 100%;
            height: 100%;
            background-color: rgb(148, 31, 31);
            transition: all 0.3s ease-in-out;
            cursor: pointer;
            &:hover {
                color: rgb(148, 31, 31);
                background-color: white;
            }
        }
}

